import * as equipErrApi from '@/api/equip_err';

export default {
  methods: {
    // 获取线路列表
    getFeederByAreaId(areaId) {
      return new Promise((resolve, reject) => {
        let req = {};
        if(areaId) {
          req.areaId = areaId;
        } else {
          req = null;
        }
        equipErrApi.getFeederByAreaId(req).then(res => {
          console.log(res);
          resolve(res);
        }).catch(err => {
          console.log(err);
          reject(err);
        })
      })
    },
    // 获取当日所有配变；
    getAllTrWarn() {
      return new Promise((resolve, reject) => {
        equipErrApi.getAllTrWarn().then((res) => {
          // console.log(res);
          // let data = res.data.resultValue.items;
          let data = res;
          this.setTrTableList(data);
          if(!data.length) {
            this.$toast('当日配变无异常')
          }
          resolve(res);
        }).catch(err => {
          console.log(err);
          reject(err);
        })
      })
    },
    // 获取当日配变异常 - 参数；
    getTodayTrWarn() {
      return new Promise((resolve, reject) => {
        equipErrApi.getTodayTrWarn(this.trWarnReq).then(res => {
          // let data = res.data.resultValue.items;
          let data = res;
          this.setTrTableList(data);
          if(!data.length) {
            this.$toast('当日配变无异常')
          }
          resolve(res);
        }).catch(err => {
          // console.log(err);
          reject(err);
        })
      })
    },
    // 获取变电站列表
    getSubstationByAreaId(areaId) {
      let req = {};
      if(areaId) {
        req.areaId = areaId;
      } else {
        req = null;
      }
      return new Promise((resolve, reject) => {
        equipErrApi.getSubstationByAreaId(req).then(res => {
          console.log(res);
          resolve(res);
        }).catch(err => {
          console.log(err);
          reject(err);
        })
      })
    },
    setTrTableList(data) {
      let tmp = [];
      data.forEach(item => {
        tmp.push({
          // 名称，异常类型，线路，
          a: item.trName,
          b: item.status,
          c: item.feederId,
          ...item
        })
      })
      this.setTableList(tmp);
    },
    setFeederTableList(data) {
      let tmp = [];
      data.forEach(item => {
        tmp.push({
          // 名称，异常类型，变电站，
          a: item.feeder_name,
          b: this.allErrsList[1][item.abnormal_type - 1].label,
          c: item.st_name,
          ...item
        })
      })
      this.setTableList(tmp);
    },
    setEnvTableList(data){
      if(data.length) {
        let tmp = [];
        data.forEach(item => {
          tmp.push({
            // 名称，异常类型，线路，
            a: item.name,
            b: item.feeder_name,
            c: item.warn_type,
            ...item
          })
        })
        this.setTableList(tmp);
      } else {
        this.setTableList([]);
        this.$toast('无环境异常');
      }
    },
    getHisTrWarn() {
      console.log(this.hisTrWarnReq);
      return new Promise((resolve, reject) => {
        equipErrApi.getHisTrWarn(this.hisTrWarnReq).then(res => {
          // let data = res.data.resultValue.items;
          let data = res;
          this.setTrTableList(data);
          resolve(res);
        }).catch(err => {
          // console.log(err);
          reject(err);
        })
      })
    },
    getTodayEnvirAbnormal() {
      return new Promise((resolve, reject) => {
        equipErrApi.getTodayEnvirAbnormal(this.envErrReq).then(res => {
          // let data = res.data.resultValue.items;
          let data = res;
          this.setEnvTableList(data);
          resolve();
        }).catch(err => {
          console.log(err);
          reject();
        })
      })
    },
    getHisEnvWarn() {
      return new Promise((resolve, reject) => {
        equipErrApi.getHisEnvirAbnormal(this.hisEnvReq).then(res => {
          // console.log(res);
          // let data = res.data.resultValue.items;
          let data = res;
          this.setEnvTableList(data);
          resolve(res);
        }).catch(err => {
          // console.log(err);
          reject(err);
        })
      })
    },
    getTodayFeederAbnormal() {
      return new Promise((resolve, reject) => {
        console.log(this.feederId, 'getTodayFeederAbnormal');
        equipErrApi.getTodayFeederAbnormal(this.feederErrReq).then(res => {
          // let data = res.data.resultValue.items;
          let data = res;
          this.setFeederTableList(data);
          if(!data.length) {
            this.$toast('当日无线路异常');
          }
          resolve(res);
        }).catch(err => {
          // console.log(err);
          reject(err);
        })
      })
    },
  }
}


